import { useState, useEffect } from 'react';
import axios from 'axios';

const useTrailData = () => {
  const [trails, setTrails] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchTrails = async () => {
      try {
        const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ForestRtBasic.aspx');
        const trails = response.data.map(trail => ({
          "": trail.TRAILID,
          "": trail.TR_CNAME,
          "": trail.TR_CLASS,
          "": trail.TR_MAIN_SYS,
          "": trail.TR_SUB_SYS,
          "": trail.TR_ADMIN,
          "": trail.TR_ADMIN_PHONE,
          "": trail.TR_POSITION,
          "": trail.TR_ENTRANCE,
          "": trail.TR_LENGTH,
          "": trail.TR_ALT,
          "": trail.TR_PAVE,
          "": trail.TR_DIF_CLASS,
          "": trail.TR_TOUR,
          "": trail.TR_BEST_SEASON,
          "": trail.TR_SPECIAL,
        }));
        setTrails(trails);
        setLoading(false);  //  loading  false
      } catch (error) {
        console.error('Error fetching trails:', error);
        setLoading(false);  //  loading  false
      }
    };
    fetchTrails();
  }, []);

  return { trails, loading };
};

export default useTrailData;
